m the top, 15% from the bottom and centered */\n  border: 1px solid #888;\n  width: 80%; /* Could be more or less, depending on screen size */\n}\n\n/* The Close Button (x) */\n\n.close {\n  position: absolute;\n  right: 25px;\n  top: 8px;\n  color: #000;\n  font-size: 35px;\n  font-weight: bold;\n}\n\n.close:hover,\n.close:focus {\n  color: red;\n  cursor: pointer;\n}\n\n/* Style the horizontal ruler */\n\nhr {\n  border: 1px solid #f1f1f1;\n  margin-bottom: 25px;\n}\n\n/* Clear floats */\n\n.clearfix::after {\n  content: \"\";\n  clear: both;\n  display: table;\n}\n\n/* Add Zoom Animation */\n\n.animate {\n  -webkit-animation: animatezoom 0.6s;\n  animation: animatezoom 0.6s\n}\n\n@-webkit-keyframes animatezoom {\n  from {-webkit-transform: scale(0)} \n  to {-webkit-transform: scale(1)}\n}\n\n@keyframes animatezoom {\n  from {transform: scale(0)} \n  to {transform: scale(1)}\n}\n\n/* Change styles for cancel button and signup button on extra small screens */\n\n@media screen and (max-width: 300px) {\n  .cancelbtn, .signupbtn {\n     width: 100%;\n  }\n}\n\n.button-block {\n  display: flex;\n}\n\n#alert-bad-head {\n  visibility: hidden;\n  padding: 20px;\n  width: 79.1%;\n  margin-left: 10%;\n  margin-bottom: 3%;\n  background-color: #f44336;\n  color: white;\n  /* position: fixed; */\n  z-index: 1;\n}\n\n#alert-bad-head.show {\n  visibility: visible;\n  /* -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;\n  animation: fadein 0.5s, fadeout 0.5s 2.5s; */\n}\n\n#alert-good-head {\n  visibility: hidden;\n  padding: 20px;\n  width: 79.1%;\n  margin-left: 10%;\n  margin-bottom: 3%;\n  background-color: #4CAF50;\n  color: white;\n  /* position: fixed; */\n  z-index: 1;\n}\n\n#alert-good-head.show {\n  visibility: visible;\n  /* -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;\n  animation: fadein 0.5s, fadeout 0.5s 2.5s; */\n}\n/*# sourceMappingURL=data:application/json;base64, */"